/**
 * Основные стили
 **/
html {
  font-size: 16px;
  color: #000; }

body {
  font-family: 'roboto_condensedregular', 'Arial', sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word; }

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto;
  background: #e9eae2; }

.header {
  display: grid;
  grid-template-columns: repeat(16, 1fr); }

.header__left {
  grid-column: span 11;
  padding-left: 15.9%;
  padding-top: 4.8%;
  padding-bottom: 8.7%;
  background: url("../img/image1.png") 100% center no-repeat;
  background-size: 100%; }
  @media (min-resolution: 2dppx), (min-width: 1500px) {
    .header__left {
      background-image: url("../img/image1-2x.png"); } }

.header__right {
  grid-column: span 5;
  background-color: #7bcbc1;
  position: relative;
  padding-top: 10.7%; }
  .header__right::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-image: url('data:image/svg+xml;utf8, <svg xmlns="http://www.w3.org/2000/svg" width="100%" height="100%"><line x1="0" y1="100%" x2="100%" y2="0" stroke="rgb(255, 255, 255)" stroke-width="1" /></svg> '); }

.header__logo {
  display: block;
  max-width: 100%; }

.header__slogan {
  font-size: 18px;
  color: #333333;
  text-transform: uppercase;
  line-height: 1;
  margin: 0;
  margin-top: 5px; }
  @media (min-width: 1500px) {
    .header__slogan {
      font-size: calc( 112.5% + (36 - 18) * (100vw - 1500px) / (3000 - 1500)); } }

.header__socials {
  margin-top: 60px;
  margin-left: 15px;
  margin-bottom: 15px; }

.header__address {
  font-size: 13px;
  color: #fff;
  text-transform: uppercase;
  line-height: 1.08;
  position: absolute;
  left: 12.9%;
  bottom: 36.5%; }
  @media (min-width: 1500px) {
    .header__address {
      font-size: calc( 81.25% + (26 - 13) * (100vw - 1500px) / (3000 - 1500)); } }

.header__contacts {
  font-size: 13px;
  color: #fff;
  text-transform: uppercase;
  line-height: 1.08;
  position: absolute;
  left: 29%;
  bottom: 15%; }
  @media (min-width: 1500px) {
    .header__contacts {
      font-size: calc( 81.25% + (26 - 13) * (100vw - 1500px) / (3000 - 1500)); } }

.header__tel,
.header__mail {
  color: #fff;
  text-decoration: none;
  line-height: 1.08;
  display: inline-block;
  transition-property: color;
  transition-duration: 500ms; }
  .header__tel:hover,
  .header__mail:hover {
    color: #f8c255; }

@media (max-width: 980px) {
  .header__left {
    grid-column: span 9; }
  .header__right {
    grid-column: span 7; } }

@media (max-width: 560px) {
  .header {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr; }
  .header__left {
    grid-column: span 1;
    grid-row: 2/3; }
  .header__right {
    grid-column: span 1;
    grid-row: 1/2; }
  .header__address {
    position: static;
    margin-left: 10%;
    margin-top: 10%; }
  .header__contacts {
    position: static;
    margin-left: 35%;
    margin-top: 20%;
    margin-bottom: 20%; } }

.socials {
  list-style: none;
  padding: 0;
  display: flex;
  align-items: flex-end; }

.socials__element {
  margin: 0 6px; }
  .socials__element:last-child {
    margin-right: 0; }

.socials__facebook {
  width: 10px;
  height: 22px;
  fill: #7bcbc1; }

.socials__twitter {
  width: 26px;
  height: 21px;
  fill: #7bcbc1; }

.socials__linkedin {
  width: 24px;
  height: 23px;
  fill: #7bcbc1; }

.socials__google {
  width: 19px;
  height: 19px;
  fill: #7bcbc1; }

.hamburger {
  background: none;
  display: block;
  cursor: pointer;
  border: 3px solid #e9eae2;
  transition-property: border-color;
  transition-duration: 500ms; }
  .hamburger:hover, .hamburger:active, .hamburger:focus {
    border-color: #f8c255;
    outline-color: #f8c255; }
    .hamburger:hover .hamburger__element, .hamburger:active .hamburger__element, .hamburger:focus .hamburger__element {
      background-color: #f8c255; }
      .hamburger:hover .hamburger__element::after, .hamburger:hover .hamburger__element::before, .hamburger:active .hamburger__element::after, .hamburger:active .hamburger__element::before, .hamburger:focus .hamburger__element::after, .hamburger:focus .hamburger__element::before {
        background-color: #f8c255; }

.hamburger__element {
  width: 40px;
  height: 3px;
  background-color: #e9eae2;
  display: block;
  margin: 0 auto; }
  .hamburger__element::after, .hamburger__element::before {
    content: '';
    width: 40px;
    height: 3px;
    background-color: #e9eae2;
    display: block;
    margin: 0 auto; }
  .hamburger__element::after {
    transform: translateY(4px); }
  .hamburger__element::before {
    transform: translateY(-7px); }

.hamburger--active {
  position: relative;
  z-index: 1;
  border-color: #f8c255; }
  .hamburger--active:hover, .hamburger--active:active, .hamburger--active:focus {
    border-color: #f8c255; }
    .hamburger--active:hover .hamburger__element, .hamburger--active:active .hamburger__element, .hamburger--active:focus .hamburger__element {
      background-color: #e9eae2; }
      .hamburger--active:hover .hamburger__element::after, .hamburger--active:hover .hamburger__element::before, .hamburger--active:active .hamburger__element::after, .hamburger--active:active .hamburger__element::before, .hamburger--active:focus .hamburger__element::after, .hamburger--active:focus .hamburger__element::before {
        background-color: #e9eae2; }
  .hamburger--active .hamburger__element {
    transform: rotate(45deg); }
    .hamburger--active .hamburger__element::after {
      transform: rotate(-90deg);
      z-index: 1;
      position: relative; }
    .hamburger--active .hamburger__element::before {
      display: none; }

.menu {
  position: absolute;
  right: 11%;
  top: 50px; }

.menu__hamburger {
  width: 119px;
  height: 114px; }

.menu__list {
  background: #f8c255;
  list-style: none;
  margin: 0;
  padding: 0;
  align-items: center;
  flex-wrap: wrap;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  min-width: 725px;
  padding-right: 119px;
  padding-left: 25px;
  box-sizing: border-box;
  display: none;
  z-index: -9999; }

.menu__list--active {
  display: flex;
  z-index: 1;
  animation-duration: 500ms;
  animation-name: menu; }

.menu__link {
  font-size: 14px;
  line-height: 1.786;
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  padding: 30px;
  transition-duration: 500ms;
  transition-property: color; }
  .menu__link:hover {
    color: #000; }
  @media (min-width: 1500px) {
    .menu__link {
      font-size: calc( 87.5% + (28 - 14) * (100vw - 1500px) / (3000 - 1500)); } }

@keyframes menu {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@media (min-width: 1500px) {
  .menu__list {
    min-width: 1100px; } }

@media (max-width: 980px) {
  .menu {
    right: 30px;
    top: 30px; } }

@media (max-width: 768px) {
  .menu {
    height: 100%; }
  .menu__hamburger {
    width: 70px;
    height: 70px; }
  .menu__list--active {
    display: block;
    min-width: auto;
    padding-right: 70px;
    padding-top: 15px;
    overflow-y: auto; }
  .menu__link {
    display: block;
    padding: 7px 80px 7px 0; } }
